import { getToken } from '@/utils/auth';
import { upUserInfo } from '@/api/user.js';
import { mapState } from 'vuex';
export default {
	data() {
		return {
			from: ''
		};
	},
	computed: {
		...mapState(['userStatus'])
	},
	methods: {
		appLoginWx() {
			let _that = this;
			uni.getProvider({
				service: 'oauth',
				success: function(res) {
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: res => {
								uni.getUserInfo({
									provider: 'weixin',
									success: info => {
										//这里请求接口
										console.log(info )
										let code = res.code;
										_that.$store
											.dispatch('codeToToken', {
												url: '/app/login',
												code: code,
												userInfo:info.userInfo
											})
											.then(() => {
												switch (_that.userStatus) {
													case 0:
														upUserInfo({
															"nickName": info.userInfo.nickName,
															"headUrl": info.userInfo.avatarUrl,
														})
															.then(res => {
																uni.showToast({
																	title: '登录成功，前往完善资料页面..',
																	icon: 'none',
																	duration: 1000,
																	success: function() {
																		setTimeout(function() {
																			uni.navigateTo({
																				url: `/pages/perfectDatum/index`
																			});
																		}, 1000);
																	}
																});
															})
															.catch(() => {
																uni.showToast({ title: '获取数据失败，请稍后再试', icon: 'none' });
															});
														break;
													case 1:
														upUserInfo({
															"nickName": info.userInfo.nickName,
															"headUrl": info.userInfo.avatarUrl,
														})
															.then(res => {
																if ( res.statusCode === 200){
																	uni.showToast({
																		title: '登录成功，前往完善资料页面..',
																		icon: 'none',
																		duration: 1000,
																		success: function() {
																			setTimeout(function() {
																				uni.navigateTo({
																					url: `/pages/perfectDatum/index`
																				});
																			}, 1000);
																		}
																	});
																}
																else{
																	uni.showToast({ title: '登录失败，请稍后再试', icon: 'none' });
																}
															})
															.catch(() => {
																uni.showToast({ title: '获取数据失败，请稍后再试', icon: 'none' });
															});
														break;
													case 2:
														uni.showToast({
															title: '登录成功，前往完善资料页面..',
															icon: 'none',
															duration: 1000,
															success: function() {
																setTimeout(function() {
																	uni.navigateTo({
																		url: `/pages/perfectDatum/index`
																	});
																}, 1000);
															}
														});
														break;
													default:
														uni.showToast({
															title: '登录成功，返回中...',
															icon: 'none',
															duration: 1000,
															success: function() {
																setTimeout(function() {
																	uni.navigateTo({
																		url: `/pages/${_that.from}/index`
																	});
																}, 1000);
															}
														});
												}
											});
									},
									fail: () => {
										uni.showToast({ title: '微信登录授权失败', icon: 'none' });
									}
								});
							},
							fail: () => {
								uni.showToast({ title: '微信登录授权失败', icon: 'none' });
							}
						});
					} else {
						uni.showToast({
							title: '请先安装微信或升级版本',
							icon: 'none'
						});
					}
				}
			});
		}
	},
	onLoad: function(option) {
		this.from = option.from;
	}
};
